#site-main {

  .content {
    a {
      border-bottom: dotted 1px $link-color;

      &:visited {
        border-bottom-color: $link-visited-color;
      }

      &:hover {
        text-decoration: none;
        border-bottom-style: solid;
        color: $link-color;
        border-bottom-color: $link-color;
      }
    }
  }

  .frame-container {
    position: relative;
    padding-bottom: 56.25%;
    margin-bottom: 1em;
    height: 0;
    overflow: hidden;

    iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: 0;
    }
  }

  .chip {
    background: transparentize($link-color, 0.8);
  }
}
